.home-wrapper {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

.main h1,
.main h2,
.main p {
  text-align: center;
}
.main h1 {
  font-size: 70px;
  font-weight: 700;
  font-family: 'Bungee Outline', sans-serif;
  letter-spacing: 2px;
}
.main h2 {
  font-size: 70px;
  font-weight: 700;
  /* text-align: center; */
}

.main h4 {
  font-size: calc(var(--font-md) + 1vmin);
}
.main p {
  /* padding: 0 50px; */
  font-size: calc(var(--font-sm) + 1vmin);
  text-align: center;
}
.main::before {
  width: 100%;
  content: ' ';
  background: url('../../../assets/kingsley.jpg') no-repeat center center/cover;
  position: absolute;
  left: 0;
  top: 20vh;
  height: 100%;
  z-index: -100;
  opacity: 0.07;
}
.home-social-links {
  margin-left: 10px;
}

.home-line {
  width: 3px;
  height: 300px;
  background-color: var(--secondary-color);
  /* transform: rotate(90deg); */
}

.home-btn {
  width: 250px;
  margin: auto;
}
.floating-btn {
  position: fixed;
  height: 40px;
  width: 40px;
  margin-top: 90vh;
  border-radius: 50%;
  background-color: var(--secondary-color);
  box-shadow: -1px 10px 59px 80px rgba(135, 253, 141, 9);
  -webkit-box-shadow: -1px 10px 59px 20px rgba(135, 253, 141, 9);
  -moz-box-shadow: -1px 10px 59px 20px rgba(135, 253, 141, 9);
  cursor: pointer;
  transition: transform 0.5s ease-in-out;
  visibility: hidden;
}
.floating-btn:hover {
  transform: scale(1.3);
}
.frame-layout__particles {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 5000;
  top: 0;
  left: 0;
  color: white;
}
.frame-layout__particles > div {
  width: 100%;
  height: 100%;
}
canvas {
  aspect-ratio: auto 1326 / 314;
}

@media only screen and (max-width: 768px) {
  .home-wrapper {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
  }
  .main {
    margin: auto;
    width: 90%;
    padding: 30px auto;
  }
  .main h1,
  .main h2 {
    font-size: 35px;
  }
  .main-text-br {
    display: none;
  }

  .main p {
    transform: scale(0.3);
  }
  .main {
    margin-top: 100px;
    height: 100%;
    overflow-y: hidden;
  }
  .floating-btn {
    visibility: visible;
  }
  .main::before {
    opacity: 0.1;
  }
  .home-social-links {
    display: none;
  }
  .floating-btn:hover .home-social-links {
    visibility: visible;
  }
  .home-line {
    display: none;
  }
}
@media (max-width: 400px) {
  .home-wrapper {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
  }
  .main h2 p {
    transform: scale(0.8);
  }
  .main h2 {
    transform: scale(0.5);
  }
  .floating-btn {
    visibility: visible;
  }
  .floating-btn:hover .home-social-links {
    visibility: visible;
  }
  .home-line {
    display: none;
  }
}
@media (max-width: 300px) {
  .home-wrapper {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
  }
  .main h2 p {
  }
  .main h2 {
    transform: scale(0.5);
  }
  .floating-btn {
    visibility: visible;
  }
  .floating-btn:hover .home-social-links {
    visibility: visible;
  }
  .home-line {
    display: none;
  }
}
