.project-container {
  padding-top: 100px;
  width: 100%;
  height: 100%;
}
.project-avatar {
  height: 470px;
  display: flex;
  align-items: center;
  overflow-y: hidden;
  justify-content: center;
  background-color: #131313;
  padding: 20px;
}
.project-avatar img {
  max-width: 100%;
  height: inherit;
  transition: all 0.5s ease-out;
}
.project-avatar img:hover {
  transform: scale(1.1);
}
.project-tags {
  display: flex;
  justify-content: start;
  align-items: center;
}
.project-tags > div {
  margin-left: 10px;
}
.project-tags > div:first-child {
  margin-left: 0;
}
.project-line {
  margin: 17px 0px;
  width: 30px;
  height: 3px;
  background-color: var(--secondary-color);
}
.cta {
  display: flex;
  justify-content: center;
  align-items: center;
}
